h2 {
    text-align: center;
    color: white;
    margin-top: 0;
    padding-top: 0%;
}

.description p {
    margin-top: 4rem;
    margin-left: 1rem;
}
.skill p {
    margin-top: -1rem;
    margin-left: 1rem;
}


.bigGrid {
    position: fixed;
}


.skill>div {
    float: left;
    margin-left: 17%;
    margin-top: 1rem;
    margin-bottom: 0rem;
    padding: 10px;
    border: 0.1px solid rgb(134, 133, 133);
    width: 60%;
    text-align: center;
    background-color: rgb(4, 4, 36);
    transition: 0.2s;

}

.skillsHeader {
    padding-top: 2rem;
    margin-left: 17%;
    margin-bottom: 0;
    text-align: left;
}


.hr1 {
    width: 40%;
    margin-top: 0.5rem;
    margin-left: 17%;
    margin-bottom: 0;
}



@media (max-width:767px) {
    .bigGrid {
        position: relative;
    }

    
.skill>div {
    float: left;
    margin-left: 20%;
    margin-top: 1rem;
    margin-bottom: 0rem;
    padding: 10px;
    border: 0.1px solid rgb(134, 133, 133);
    width: 60%;
    text-align: center;
}

.skillsHeader {
    padding-top: 2rem;
    margin-left: 5%;
    margin-bottom: 0;
    text-align: left;
}


.hr1 {
    width: 40%;
    margin-top: 0.5rem;
    margin-left: 5%;
    margin-bottom: 0;
}
    
}
