.contact-body {
    margin-top: 1em;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: .1em;
}
.contact-grid {
    text-align: center;
    justify-content: center;
    width: 100%;
    color: black;
    margin-bottom: 20px;
}
  
.contact-grid h2 {
    font-weight: bold;
}
  
  
.contact-grid hr{
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}
  

.phone {
      width: 3rem;
      height: 1.5rem;
      padding-right: 1rem;
      padding-left: 1rem;
}
  
h2 {
      color: white;
}
.email {
      width: 5rem;
      height: 1.5rem;
      padding-right: 1.5rem;
      padding-left: 1.5rem;
}
.ins {
    width: 4rem;
      height: 3rem;
      padding-right: 2rem;
      padding-left: 6rem;
}
.facebook {
    width: 6rem;
      height: 4rem;
      padding-right: 2rem;
      padding-left: 4.8rem;
}