
@font-face {
  src: url(../fonts/Montserrat-Light.ttf);
  font-family: Mont;
}


@font-face {
  src: url(../fonts/Hind-Light.ttf);
  font-family: Hind;
}



.landing-grid {
background: rgb(0, 0, 12);
text-align: center;
}

.kobe-img {
  height: 300px;
  padding-top: 3em;
  max-width: 300px;
  margin: 0 auto;
  margin-bottom: 2%;
}

.banner-text {
  background-color: transparent;
  width: 75%;
  margin: auto;
  border-radius: 20px;
  margin-top: 2%;
}

.banner-text h1 {
  font-size: 46px;
  font-weight: bold;
  color: white;
  text-align: center;
  padding-top: .8em;
  margin-bottom:0
}

.banner-text h3 {
  padding-top: 0em;
  font-size: 36px;
  color: white;
  text-align: center;
  margin-top:0
}

.banner-text h5 {
  padding-top: 2em;
  font-size: 18px;
  color: white;
  text-align: center;
  margin-top:0
}


.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 24px;
  padding-bottom: 0em;
  padding-top: 1em;
  text-align: center;
  font-family: Hind;
  margin-bottom: 0;
}

.social-link {
  display: flex;
  justify-content: space-between;
  width: 30%;
  margin: auto;
}


h2 {
  font-family: Mont;
}


@media (max-width:767px) {
  .kobe-img {
    height: 200px;
    padding-top: 3em;
    max-width: 200px;
    margin: 0 auto;
    margin-bottom: 2%;
  }

  
.banner-text p {
  color: white;
  font-size: 16px;
  padding-bottom: 2em;
  padding-top: 1em;
  text-align: center;
  font-family: Hind;
}


.banner-text h3 {
  padding-top: 1em;
  font-size: 36px;
  color: white;
  text-align: center;
  margin-top:0
}

}





