@font-face {
    src: url(../fonts/Montserrat-Light.ttf);
    font-family: Mont;
}
  
  
@font-face {
    src: url(../fonts/Hind-Light.ttf);
    font-family: Hind;
}
  
  




.row {
    display: flex;
    margin-top: 2rem;
}

.row>.col-md-4>div {
    width: 22rem;
    height: 22rem;
    background-color: rgb(42, 42, 46);
}

.row>.col-md-4>div h5 {
    font-size: 16px;
    font-family: Mont;
    font-weight: 600;
}


.row>.col-md-4>div a {
    margin-top: 5%;
    background-color: rgb(49, 47, 47);
    color: white;
    border-radius: 5px;
    border: 1px solid;
    margin-left: 9%;
    padding: 6px 28px;
    transition: 0.3s;
}

.row>.col-md-4>div a:hover {
    background-color: white;
    color: black;
}


@media (max-width:767px) {
    .row>.col-md-4 {
        width: 87%;
        margin-left: 6.5%;    
    }


    .row>.col-md-4>div {
        width: 100%;
        height: 22rem;
        background-color: rgb(28, 28, 31);
    }

    
    .row>.col-md-4>div h5 {
        font-size: 14px;
        font-family: Mont;
        font-weight: 600;
    }

    .row>.col-md-4>div a {
        margin-top: 7%;
        background-color: rgb(49, 47, 47);
        color: white;
        border-radius: 5px;
        border: 0.1px solid blue;
        margin-left: 7%;
        padding: 6px 28px;
        transition: 0.3s;
        font-size: 3.3vw;
    }

    .row>.col-md-4>div a:nth-child(2) {
        border: 0.1px solid green;
    }
}




